.alco-counter {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  font-weight: 500;

  &__header {
    text-align: center;
    margin: 0;
    font-size: $font-size--h1;
    font-weight: $font-weight--h1;

    &--white-theme {
      color: $header-color--white-theme;
      text-shadow: $shadow-size--sm
        $shadow-color--white-theme;
    }
    &--dark-theme {
      color: $header-color--white-theme;
      text-shadow: none;
    }
  }
  &__show-calendar-btn {
    &--white-theme {
      box-shadow: $shadow-size--md $shadow-blur--md
        $shadow-color--white-theme;
    }
  }

  &__cleaner {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 15px;
    // border-radius: 5px;
    // border: 1px solid grey;

    button {
      border: 5px;
      margin: 5px;
      border-radius: 5px;
      color: rgb(200, 0, 0);
      &:hover {
        background-color: rgb(217, 217, 217);
      }
    }
    &--white-theme {
      button {
        box-shadow: $shadow-size--md $shadow-blur--md
          $shadow-color--white-theme;
      }
    }
    &--dark-theme {
      button {
        box-shadow: none;
      }
    }
  }
}
