$button--brd-radius: 5px;
$cell--brd-radius: 5px;
$box--brd-radius: 5px;

$font-size--h1: 2rem;
$font-size--h2: 1.75rem;
$font-size--p: 1rem;

$font-weight--h1: 600;
$font-weight--h2: 500;

$header-color--white-theme: rgb(26, 100, 219);
$header-color--dark-theme: rgb(0, 58, 150);

$text-color--white-theme: black;
$text-color--dark-theme: white;

$shadow-color--white-theme: #a7a7a7;
$shadow-color--dark-theme: none;

$tabel-bg-color--white-theme: rgb(244, 244, 244);
$display-bg-color--white-theme: aquamarine;
$box-border-color--white-theme: grey;
$box-border-color--dark-theme: rgb(213, 213, 213);

$shadow-size--sm: 3px 3px 3px;
$shadow-size--md: 3px 7px 5px;
$shadow-size--lg: 5px 10px 8px;

$shadow-blur--sm: 3px;
$shadow-blur--md: 5px;
$shadow-blur--lg: 10px;

